body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.alerts-container {
    position: fixed;
    right: 0;
    top: 0;
    padding: 20px;
    width: 350px;
    max-height: 100%;
    z-index: 999999;
    overflow: auto;
}

.alerts-container .alert {
    position: relative;
    overflow: hidden;
}

.alerts-container .alert .close {
    position: absolute;
    top: 3px;
    right: 5px;
    outline: none;
}

.alerts-container .alert.transition-enter {
    -webkit-transform: scale(0);
            transform: scale(0);
}

.alerts-container .alert.transition-enter-active {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: all 300ms cubic-bezier(0, 0, 0.5, 1.5);
    transition: all 300ms cubic-bezier(0, 0, 0.5, 1.5);
}

.alerts-container .alert.transition-exit {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.alerts-container .alert.transition-exit-active {
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-transition: all 300ms ease-in-out;
    transition: all 300ms ease-in-out;
}

